.header
  padding: 15px 30px
  display: flex
  justify-content: center
  background-color: $coral
  position: relative
  height: 74px
  border-bottom: 2px solid $black
  box-shadow: 0px 20px 20px -5px rgba(0,0,0,0.1), 0px 8px 10px -6px rgba(0,0,0,0.1)
  

  &__inner
    display: flex
    justify-content: space-between
    align-items: center
    max-width: 1840px
    width: 100%

  &__btn
    display: none

  &__logo
    opacity: 0
    line-height: 0

    &:hover
      @media #{$mouse-device}
        opacity: 0.7
        transition: all 0.3s ease

    &:active
      opacity: 0.5
      transition: all 0.3s ease

  &__links
    display: flex
    justify-content: center
    gap: 40px
    list-style-type: none
    padding: 0
    margin: 0
    font-family: $lg
    font-weight: 400
    
  
  &__link 
    text-decoration: none
    color: $white
    font-family: $lg
    font-weight: 400
    font-size: 24px
    letter-spacing: 0.6px
    border-bottom: 3px solid $coral
    transition: all 0.3s ease

    &:hover
      @media #{$mouse-device}
        color: $white
        border-bottom: 3px solid $white

    &:active
      color: $white
      border-bottom: 3px solid $white

    &:not(:hover)
      border-bottom: 3px solid $coral

  &__lang
    position: relative
    display: flex
    align-items: center
    text-decoration: none
    color: $black
    font-family: $lg
    font-weight: 400
    font-size: 20px
    letter-spacing: 0.6px
    height: 46px
    background-color: $white
    border: 2px solid $black
    padding: 11px 10px 11px 72px

    &:hover
      @media #{$mouse-device}
        opacity: 0.7
        transition: all 0.3s ease

    &:active
      opacity: 0.5
      transition: all 0.3s ease

    &::before
      content: ''
      position: absolute
      left: 0
      top: 0
      width: 58px
      height: 43px
      background-repeat: no-repeat
      background-size: 58px 42px
      
    &--tr::before
      background-image: url('img/tr.svg')

    &--en::before
      background-image: url('img/en.svg')
    

@media screen and (max-width: 980px)
  .header
    &__links
      gap: 20px

    &__link
      font-size: 18px

    &__lang
      height: 40px
      padding: 11px 10px 11px 64px
      
      &::before
        width: 51px
        height: 38px
        background-size: 100%

  
@media screen and (max-width: 790px)
  .header
    position: relative
    height: 54px

    &__link:hover
      @media #{$mouse-device}
        color: $coral
        border-bottom: 3px solid $white

    &__link:active
      color: $coral
      border-bottom: 3px solid $white

    &__link:not(:active)
      color: $black
      border-bottom: 3px solid $white
      

    &__btn
      display: block
      width: 30px
      height: 30px
      background-size: 30px
      border: none
      background-repeat: no-repeat
      background-color: transparent

      &-open
        background-image: url('img/open-menu.svg')

      &-close
        background-image: url('img/close-menu.svg')

    &__nav,
    &__lang
      display: none

    .opened &__nav
      position: absolute
      display: flex
      flex-direction: column
      justify-content: flex-start
      background-color: $white
      width: 100%
      top: 54px
      left: 0
      z-index: 99999

    .opened &__links
      padding: 32px 30px
      flex-direction: column
      justify-content: flex-start
      background-color: $white
      width: 100%
      top: 54px
      left: 0
      z-index: 99999

    .opened &__lang
      display: flex
      position: absolute
      top: 84px
      right: 30px
      z-index: 9999999
      



@media screen and (max-width: 480px)
  .header
    padding: 15px

    &--opened &__links
      padding: 32px 15px

    &--opened &__lang
      top: 84px
      right: 15px