.plans
  background-color: $mustard
  display: flex
  justify-content: center
  align-items: center
  width: 100%
  border-bottom: 4px solid $black

  &__inner
    padding: 100px 30px 200px 30px
    max-width: 1300px

  &__h2
    font-family: $lg
    color: $white
    font-size: 100px
    font-weight: 400
    -webkit-text-stroke: 2px $black
    text-stroke: 2px $black
    margin: 0
    margin-bottom: 70px
    text-shadow: $shadow
    text-align: center
  
  &__element
    &:nth-of-type(1),
    &:nth-of-type(4)
      transform: rotate(-$degree)

    &:nth-of-type(2),
    &:nth-of-type(3)
      transform: rotate($degree)
    
    &:nth-of-type(2),
    &:nth-of-type(4)
      margin-top: 20px

  &__plan
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    gap: 20px
    background-color: $pale
    border: 3px solid $black
    max-width: 580px
    border-radius: 20px
    padding: 30px 30px 80px 30px
    

  &__h3
    font-family: $lg
    color: $black
    font-size: 30px
    line-height: 36px
    font-weight: 400
    margin: 0
    text-align: center

  &__ul
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    list-style-type: none
    gap: 20px
    padding-left: 0
    

  &__li
    font-family: $arial
    color: $black
    font-size: 16px
    font-weight: 700
    line-height: 1.5
    text-align: center


  &__list
    display: flex
    flex-wrap: wrap
    flex-direction: row
    justify-content: center
    align-items: flex-start
    list-style-type: none
    padding-left: 0
    gap: 40px

  &__img
    height: fit-content



@media screen and (max-width: 1260px)
  .plans
    &__inner
      padding: 100px 30px
    
    &__element
      &:nth-of-type(1),
      &:nth-of-type(3)
        transform: rotate(-$degree)

      &:nth-of-type(2),
      &:nth-of-type(4)
        transform: rotate($degree)

    &__h2
      text-align: center
      font-size: 80px

    &__join
      font-size: 28px
      color: $navy



@media screen and (max-width: 980px)
  .plans

    &__inner
      padding: 60px 30px
      gap: 36px

    &__h2
      text-align: center
      font-size: 64px


@media screen and (max-width: 680px)
  .plans

    &__h2
      text-align: center
      font-size: 48px

    &__join
      font-size: 24px

  
@media screen and (max-width: 480px)
  .plans
    &__inner
      padding: 40px 30px
      gap: 24px

    &__h2
      text-align: center
      font-size: 36px
      text-shadow: 2px 2px rgba(0,0,0,1)
      margin-bottom: 30px

    &__h3
      font-size: 23px
      line-height: 1

    &__list
      gap: 20px

    &__ul
      gap: 16px

    &__li
      font-size: 12px

    &__plan
      padding: 28px 28px 48px 28px



@media screen and (max-width: 360px)
  .plans
    &__inner
      padding: 30px 30px
      gap: 21px
