.ticker
  padding: 27px 0
  background-color: $white
  max-height: 54px
  width: 100%
  overflow: hidden
  display: flex
  border-top: 2px solid $black
  border-bottom: 2px solid $black

  &__inner
    display: flex
    flex-direction: row
    justify-content: flex-start
    align-items: center
    gap: $gap-ticker

  &__text
    font-family: $lg
    font-size: 24px
    font-weight: 400
    color: $black
    margin: 0
    line-height: 0
    white-space: nowrap
    text-transform: uppercase
    animation: ticker 7s infinite linear

  &:hover &__inner &__text
    @media #{$mouse-device}
      animation-play-state: paused


  @keyframes ticker
    100%
       transform: translateX(calc(-100% - $gap-ticker))


@media(max-width: 480px)
  .ticker
    padding: 22px 0
    gap: 24px

    &__text
      font-size: 24px
