.main
  position: relative
  display: flex
  flex-direction: column
  align-items: center

  &.dark::before
    content: ''
    width: 100%
    height: 100%
    top: 0
    left: 0
    position: absolute
    background-color: rgba(0,0,0,0.5)
    z-index: 9999

  &__img
    width: 100%
    height: auto

  &__divider
    width: 100%
    height: auto
    margin-top: -8vw



@media screen and (max-width: 480px)
  .main
    min-width: 320px
    width: 100%
  