.video
  width: 100%
  height: 100%
  background-color: $coral--light
  display: flex
  justify-content: center
  border-top: 4px solid $black
  border-bottom: 4px solid $black

  &__inner
    display: flex
    flex-direction: column
    gap: 30px
    justify-content: center
    align-items: center
    padding: 100px 30px 100px 30px

  &__h2
    font-family: $lg
    color: $white
    font-size: 64px
    font-weight: 400
    -webkit-text-stroke: 2px $black
    text-stroke: 2px $black
    margin: 0
    margin-bottom: 20px
    text-shadow: $shadow
    text-align: center
    max-width: 1024px
    line-height: 1.25

  & iframe
    aspect-ratio: 16 / 9
    width: 100% !important
    max-width: 560px


@media screen and (max-width: 1260px)
  .video

    &__h2
      text-align: center
      font-size: 80px


@media screen and (max-width: 980px)
  .video

    &__inner
      padding: 60px 30px
      gap: 36px

    &__h2
      text-align: center
      font-size: 64px


@media screen and (max-width: 680px)
  .video

    &__h2
      text-align: center
      font-size: 48px


@media screen and (max-width: 480px)
  .video
    &__inner
      padding: 40px 30px
      gap: 10px

    &__h2
      text-align: center
      font-size: 36px
      text-shadow: 2px 2px rgba(0,0,0,1)