.hero
  position: relative
  display: flex
  justify-content: center
  padding: 0
  width: 100%
  background-color: $coral--light
  background-image: url('img/rays.svg')
  background-repeat: no-repeat
  background-size: cover
  background-position-x: 50%


  &__inner
    display: flex
    flex-direction: column
    padding-top: 50px
    padding-left: 30px
    padding-right: 30px

  &__man
    height: fit-content

  &__kanka
    margin-bottom: -160px
    height: fit-content

    
@media screen and (max-width: 680px)
  .hero
    background-position-x: 50%
    background-size: 200% 100%

    &__kanka
      margin-bottom: -21vw


@media screen and (max-width: 480px)
  .hero
    background-position-x: 50%
    background-size: 200% 100%

    &__inner
      padding-top: 40px

    &__kanka
      margin-bottom: -6vw

        