.socials
  background-color: $pale
  display: flex
  justify-content: center
  align-items: center
  width: 100%
  border-bottom: 4px solid $black

  &__inner
    padding: 100px 30px 100px 30px
    max-width: 1300px
    display: flex
    align-items: center
    flex-direction: column
    gap: 70px

  &__h2
    font-family: $lg
    color: $black
    font-size: 100px
    font-weight: 400
    margin: 0
    text-align: center
    line-height: 0.8

  &__h3,
  &__join
    font-family: $arial
    color: $black
    font-size: 30px
    margin: 0
    text-decoration: none
    text-align: center
    border-bottom: 3px solid $pale
    transition: all 0.3s ease

    &:hover
      @media #{$mouse-device}
        color: $coral
        border-bottom: 3px solid $coral

    &:active
      color: $coral
      border-bottom: 3px solid $coral

    &:not(:hover)
      border-bottom: 3px solid $pale

  &__ul
    display: flex
    gap: 24px
    list-style-type: none
    padding-left: 0
    margin: 0

  &__li
    width: 64px
    height: 64px
    background-color: $black
    transition: all 0.3s ease
    
    &:hover
      @media #{$mouse-device}
        transform: scale(1.1)

    &:active
      transform: scale(1.1)

    &:nth-of-type(1):not(:hover),
    &:nth-of-type(3):not(:hover)
      transform: rotate(-$degree--socials)

    &:nth-of-type(2):not(:hover),
    &:nth-of-type(4):not(:hover)
      transform: rotate($degree--socials)

  &__link    
    background-repeat: no-repeat
    background-size: 64px 64px
    width: 64px
    height: 64px
    display: block
    transition: all 0.3s ease

    &:hover
      @media #{$mouse-device}
        transform: scale(1.1)

    &:active
      transform: scale(1.1)

    &:not(:hover)
      transform: scale(1)

    &--twitter
      background-image: url('img/icon--twitter.svg')

    &--telegram
      background-image: url('img/icon--telegram.svg')

    &--instagram
      background-image: url('img/icon--instagram.svg')

    &--tiktok
      background-image: url('img/icon--tiktok.svg')



@media screen and (max-width: 1200px)
  .socials
    &__inner
      padding: 100px 30px 100px 30px

    &__h2
      text-align: center
      font-size: 80px

    &__join
      font-size: 28px
      color: $navy



@media screen and (max-width: 980px)
  .socials

    &__inner
      padding: 60px 30px
      gap: 36px

    &__h2
      text-align: center
      font-size: 64px


@media screen and (max-width: 680px)
  .socials

    &__h2
      text-align: center
      font-size: 48px
      color: $black

    &__join
      font-size: 24px

  
@media screen and (max-width: 480px)
  .socials
    &__inner
      padding: 40px 15px
      gap: 24px

    &__h2
      text-align: center
      font-size: 36px

    &__join
      font-size: 14px

    &__link
      width: 58px
      height: 58px
      background-size: 58px 58px


    &__li
      width: 58px
      height: 58px
      background-size: 58px 58px


@media screen and (max-width: 360px)
  .socials
    &__inner
      padding: 30px 15px
