.about
  background-color: $pale
  display: flex
  justify-content: center
  align-items: center
  width: 100%

  &__inner
    padding: 100px 30px 200px 30px
    max-width: 1300px

  &__h2
    font-family: $lg
    color: $white
    font-size: 100px
    font-weight: 400
    -webkit-text-stroke: 2px $black
    text-stroke: 2px $black
    margin: 0
    margin-bottom: 25px
    text-shadow: $shadow

  &__p
    font-family: $arial
    font-size: 20px
    line-height: 1.5
    font-weight: 700
    color: $navy

  &__container
    display: flex
    gap: 30px

  &__img
    width: 70%
    height: fit-content
    

@media screen and (max-width: 1200px)
  .about
    &__inner
      padding: 100px 30px 100px 30px

    &__img
      width: 50%


    &__container
      flex-direction: column-reverse
      justify-content: center
      align-items: center

    &__h2
      text-align: center
      font-size: 80px

    &__img
      width: 100%


@media screen and (max-width: 980px)
  .about

    &__inner
      padding: 60px 30px

    &__h2
      text-align: center
      font-size: 64px


@media screen and (max-width: 680px)
  .about

    &__h2
      text-align: center
      font-size: 48px
      text-shadow: 2px 2px $black

    &__p
      font-size: 16px

  
@media screen and (max-width: 480px)
  .about
    &__inner
      padding: 40px 15px

    &__h2
      text-align: center
      font-size: 36px

    &__p
      font-size: 14px


@media screen and (max-width: 360px)
  .about
    &__inner
      padding: 30px 15px